* {
    font-family: Arial !important;
}

.history-list {
    border: 1px solid lightgray;
    min-height: 45px;
    overflow-y: scroll;
    max-height: 120px;
    padding: 5px;
    color: gray;
}

tr:hover {
    background-color: #fafafa;
}

table {
    font-size: 2em;
    font-family: arial;
    width: 100%;
}

.container {
    margin: auto;
    margin-top: 50px;
    width: 500px;
    padding: 0 20px 20px 20px;
    border: 1px solid lightgray;
    border-radius: 5px;
    background-color: white;
}

button {
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 20px;
    height: 50px;
    background-color: #fafafa;
    border: 1px solid lightgray;
}

.display {
    display: -ms-flexbox;
    display: flex;
    outline: none;
}

input {
    padding: 10px 15px 10px 15px;
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 2em;
    text-align: right;
    border: 1px solid lightgray;
    background-color: #fafafa;

}

.row {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}


